import { Component, OnInit, EventEmitter, Output, QueryList, ViewChildren, Input } from '@angular/core';
import { DependencyTableHelisaService, ConfigTable } from './dependency-table-helisa.service';
import { Cell, ColumnConfig, EventColumn, RequestTableHelisa, SelectObject, TotalTableHelisa } from '../table-helisa/table-helisa.interface';
import { TableHelisaService } from '../table-helisa/table-helisa.service';
import { TableHelisaComponent } from '../table-helisa/table-helisa.component';

export interface EventDependency<T> {
  index: number;
  data: T;
}

export enum TypeResizeEnum {
  BOTH,
  ONLY_CELLS,
  ONLY_TABLES
}

export class ResizeConfig {
  enableResize: boolean;
  typeResize: TypeResizeEnum;
  uuid: string;

  constructor() {
    this.enableResize = false;
    this.typeResize = TypeResizeEnum.ONLY_CELLS;
    this.uuid = 'testing';
  }
}

export class ResizeResponse {
  quantityTable!: number;
  uuid: string;
}

@Component({
  selector: 'hel-dependency-table',
  templateUrl: './dependency-table-helisa.component.html',
  styleUrls: ['./dependency-table-helisa.component.sass'],
  providers: [DependencyTableHelisaService]
})
export class DependencyTableHelisaComponent<T> implements OnInit {

  tables: Array<ConfigTable<T>> = [];
  @ViewChildren('viewTables') viewTables: QueryList<TableHelisaComponent<T>>;
  @Input() showToolTip: boolean = true;

  /**
   * deprecated, use selectObject
   */
  @Output() selected: EventEmitter<EventDependency<T>> = new EventEmitter<EventDependency<T>>();
  @Output() selectObject: EventEmitter<EventDependency<{} | T>> = new EventEmitter<EventDependency<{} | T>>();
  @Output() nextPage: EventEmitter<EventDependency<{} | T>> = new EventEmitter<EventDependency<{} | T>>();
  @Output() total: EventEmitter<EventDependency<{} | T>> = new EventEmitter<EventDependency<{}>>();
  @Output() sort: EventEmitter<EventDependency<{} | T>> = new EventEmitter<EventDependency<{}>>();
  @Output() drop: EventEmitter<EventDependency<{} | T>> = new EventEmitter<EventDependency<{}>>();
  @Output() addRow: EventEmitter<number> = new EventEmitter<number>();
  @Output() selectCell: EventEmitter<EventDependency<{} | T>> = new EventEmitter<EventDependency<{} | T>>();
  @Output() bookClicked: EventEmitter<EventDependency<{} | T>> = new EventEmitter<EventDependency<{} | T>>();
  @Output() afterViewInit: EventEmitter<ResizeResponse> = new EventEmitter<ResizeResponse>();
  selectedObject: EventDependency<{}> = null;


  /**
   * Tiempo antes de ocultarla el mensaje del tooltip
   */
  @Input() hideDelay: number = 600;

  /**
   * Tiempo antes de mostra el mensaje del tooltip
   */
  @Input() showDelay: number = 500;
  @Input() resizeConfig: ResizeConfig = new ResizeConfig();


  constructor(private dependencyTableHelisaService: DependencyTableHelisaService<T>, private tableService: TableHelisaService<T>) {
  }

  get resizingConfig(): ResizeConfig {
    return this.resizeConfig;
  }

  ngOnInit(): void {
    this.getTables();
    this.dependencyTableHelisaService.emitNextPage.subscribe(
      (event: EventDependency<T[]>): void => {
        this.tableService.addPage(event.data, this.viewTables.toArray()[event.index]);
      }
    );

    this.dependencyTableHelisaService.emitTotal.subscribe(
      (event: EventDependency<TotalTableHelisa>): void => {
        this.tableService.setTotal(event.data, this.viewTables[event.index]);
      }
    );

    // Observable para mostrar o esconder el boton de una tabla
    this.dependencyTableHelisaService.emitVisibilityButton.subscribe(
      (data: EventDependency<boolean>): void => {
        if (!!data && data.index !== undefined) {
          const table: ConfigTable<T> = this.tables[data.index];
          if (!!table) {
            table.addRowButton.showButton = data.data;
          }
        }
      }
    );

    // Observable para habilitar o deshabilitar el botón y mostrar titulo
    this.dependencyTableHelisaService.emitEnabledButton.subscribe(
      (data: EventDependency<{ isDisabled: boolean, text: string }>): void => {
        if (!!data && data.index !== undefined) {
          const table: ConfigTable<T> = this.tables[data.index];
          if (!!table) {
            table.addRowButton.isDisabled = data.data.isDisabled;
            table.addRowButton.toolTipText = data.data.text;
          }
        }
      }
    );

    // Observable para mostrar o esconder los botones de todas las tablas
    this.dependencyTableHelisaService.emitVisibilityAllButtons.subscribe(
      (data: boolean): void => {
        if (data !== undefined && data != null) {
          this.tables.forEach((element: ConfigTable<T>): void => {
            if (!!element.addRowButton) {
              element.addRowButton.showButton = data;
            }
          });
        }
      }
    );

    // Observable para manejo de selección de celdas
    this.dependencyTableHelisaService.emitIsCellSelection.subscribe(
      (data: EventDependency<boolean>): void => {
        if (!!data && data.index !== undefined) {
          const table: ConfigTable<T> = this.tables[data.index];
          if (table) {
            table.isCellSelection = data.data;
          }
        }
      });

    // Observable para manejo de columnas
    this.dependencyTableHelisaService.emitChangeColumns.subscribe(
      (data: EventDependency<ColumnConfig[]>): void => {
        if (!!data && data.index !== undefined) {
          const table: ConfigTable<T> = this.tables[data.index];
          if (table) {
            table.columns = data.data;
          }
        }
      });
  }

  /**
   * retorna el servicio que gestiona el componente.
   */
  getService(): DependencyTableHelisaService<T> {
    return this.dependencyTableHelisaService;
  }

  /**
   * Obtiene un observable con las tablas dependientes desde el servicio.
   */
  getTables(): void {
    this.dependencyTableHelisaService.getTables()
      .subscribe((tables: ConfigTable<T>[]): void => {
        this.tables.splice(0, this.tables.length, ...tables);
        this.viewTables.forEach((item: TableHelisaComponent<T>): void => {
          item.reload();
        });
      }
      );
  }

  /**
   * Evento que se dispara desde una tabla, emitiendo un nuevo evento con el inidice de la tabla que dispara el evento y el evento generado.
   * @param index indica el indice de la tabla seleccionada
   * @param data retorna la fila que fue seleccionada
   */
  onSelectedDependency(index: number, event: SelectObject<T>): void {
    this.selectedObject = { index, data: event };
    this.selected.emit({ index, data: event.value });
    this.selectObject.emit({ index, data: event });
  }

  /**
   * Evento que se dispara desde una tabla, emitiendo un nuevo evento con el inidice de la tabla que dispara el evento y el evento generado.
   * @param index indica el indice de la tabla que genera el evento
   * @param event evento generado desde la tabla
   */
  onNextPage(index: number, event: RequestTableHelisa<T>): void {
    this.nextPage.emit({ index, data: event });
  }

  /**
   * Evento que se dispara desde una tabla, emitiendo un nuevo evento con el inidice de la tabla que dispara el evento y el evento generado.
   * @param index indica el indice de la tabla que genera el evento
   * @param event evento generado desde la tabla
   */
  onTotal(index: number, event: EventColumn): void {
    this.total.emit({ index, data: event });
  }

  /**
   * Evento que se dispara desde una tabla, emitiendo un nuevo evento con el inidice de la tabla que dispara el evento y el evento generado.
   * @param index indica el indice de la tabla que genera el evento
   * @param event evento generado desde la tabla
   */
  onSort(index: number, event: EventColumn): void {
    this.sort.emit({ index, data: event });
  }

  /**
   * Evento que se dispara desde una tabla, emitiendo un nuevo evento con el inidice de la tabla que dispara el evento y el evento generado.
   * @param index indica el indice de la tabla que genera el evento
   * @param event evento generado desde la tabla
   */
  onDrop(index: number, event: EventDependency<T>): void {
    this.drop.emit({ index, data: event });
  }

  /**
   * Evento que se dispara desde una tabla, emite el indice de la tabla al cual se le debe añadir una nueva fila
   * @param index indica el indice de la tabla de la cual se dispara el evento
   */
  onAddRow(index: number): void {
    this.addRow.emit(index);
  }

  selectedCell(index: number, event: Cell<T>): void {
    if (this.tables[index].isCellSelection) {
      this.selectCell.emit({ index, data: event });
    }
  }

  onBookClicked(index: number, event: EventDependency<T>): void {
    this.bookClicked.emit({ index, data: event });
  }

  onAfterViewInitTable(resizeResponse: ResizeResponse): void {
    if (this.resizeConfig.enableResize) {
      this.afterViewInit.emit({
        quantityTable: this.tables.length,
        uuid: resizeResponse.uuid
      } as ResizeResponse);
    }
  }
}
